<template>
  <div
    v-on:click="onRoomClick(room.id)"
    :class="{'wrapper': true, active: room.id === activeRoomId }"
  >
    <div
      class="conversation-row"
      v-if="otherMembers.length == 1"
    >
      <div
        class="room-thumbnail"
      >
        <img
          v-if="otherMembers[0].user.thumbnail"
          :src="hostName + otherMembers[0].user.thumbnail"
        >
        <img
          v-else
          src="@/assets/images/no-image-available.png"
        >
      </div>
      <span
        class="conversation-name"
      >
        {{ otherMembers[0].user.name }}
      </span>
    </div>
    <div
      class="conversation-row"
      v-if="otherMembers.length > 1"
    >
      <div
        class="room-thumbnail"
      >
        <img
          src="@/assets/images/group-thumbnail.png"
        >
      </div>
      <span
        class="conversation-name"
      >
        {{ room.title }}
      </span>
    </div>
  </div>
</template>
<style scoped>
.wrapper {
  display: relative;
  padding: 0px;
}
.room-thumbnail {
  display: inline-block ;
}
.conversation-row {
  font-size: 12px;
  display:flex;
  padding: 0 20px;
  position :relative;
  background-color: transparent;
  height: 50px;
  color: white;
}
.active {
  background-color: #DD604C;
  }
.room-thumbnail {
  padding: 5px 0;
  max-width: 30%;
  text-align: left;
}
.room-thumbnail > img {
  height:100%;
  width: auto;
  border-radius: 50%;
}
.conversation-name {
  font-size: 14px;
  padding: 0 10px;
  text-align: left;
  line-height: 50px;
}

.conversation-member {
  display: flex;
  padding-left: 30%;
  height: 25px;
  margin: 5px 0;
}
.member-thumbnail {
  display: inline-block;
  font-size: 10px;
}

.member-name {
  padding-left: 10px;
  color: white;
  font-size: 12px;
  line-height: 25px;
}

</style>
<script>
import moment from 'moment'
import basemixin from '../mixins/basemixin'
export default {
  mixins: [basemixin],
  data () {
    return {
      hideMembers: false
    }
  },
  props: {
    room: { type: Object, requred: true }
  },
  computed: {
    otherMembers: function () {
      return this.room.members.filter((member) => {
        return (member.user.id !== this.userInfo.id)
      })
    }
  },
  filters: {
    format: function (value) {
      return moment(value * 1000).format('YYYY/MM/DD HH:mm')
    }
  },
  methods: {
    onRoomClick: function (id) {
      if (id !== this.activeRoomId) {
        this.$store.dispatch('chat/enter', { roomId: id })
      }
    }
  }
}
</script>



