<template>
  <div
    :class="{ 'side-panel': true, 'active': currentPage === 'room'}"
  >
    <vue-loaders v-show="loading" name="ball-pulse" color="orange" scale="1"></vue-loaders>
    <div class="my-info" v-show="!loading">
      <img
        class="my-thumbnail"
        v-if="userInfo.thumbnail"
        :src="userInfo.thumbnail"
      >
      <img
        class="my-thumbnail"
        v-else
        src="@/assets/images/no-image-available.png"
      >
      <span class="my-name">{{ userInfo.name }}</span>
    </div>
    <div
      v-if="rooms.length > 0"
    >
      <RoomsSectionRow
        v-for="(room, id) in rooms"
        :key="id"
        :room=room
      />
    </div>
    <span
      class="message"
      v-else
    >
     参加中のルームはありません
    </span>

  </div>
</template>
<style scoped>
  div.side-panel {
    width: 20vw;
    height:100%;
    background-color: #EC654F;
    z-index: 1;
    border-right: solid red 1px;
  }
  .my-info {
    display: flex;
    padding: 20px;
    border-bottom: solid lightgray 1px;
  }
  .my-thumbnail {
    width:40px;
    height: 40px;
    border-radius: 50%;
    display: inline-block;

  }
  .my-name {
    display:inline-block;
    line-height: 40px;
    padding-left: 20px;

    color: white;
    font-weight: bold
  }
  .message {
    color:white;
    
  }
  @media screen and (max-width: 480px) {
    div.side-panel {
      display: none
    }
    div.side-panel.active {
      width: 100%;
      display: block;
    }
  }

</style>
<script>
import RoomsSectionRow from './RoomsSectionRow'
import basemixin from '../mixins/basemixin'
export default {
  mixins: [basemixin],
  components: {
    RoomsSectionRow
  },
  props: {
  },
  watch: {
    'socket.isConnected': function (data) {
      console.log('socket status changed get rooms')
      if (data && !this.socket.isReconnected) {
        this.$store.dispatch('chat/getRooms', {})
      }
    },
    activeRoomId: async function (newVal, oldVal) {
      this.$store.dispatch('chat/getRoomFiles')
      this.$store.dispatch('chat/clearCurrentMessages')
      this.$store.dispatch('chat/getNextRoomMessages')
      if (oldVal) {
        this.$store.dispatch('chat/leave', {roomId: oldVal})
      }
      // for mobile design
      this.$store.dispatch('page/goto', 'chat')
    }
  },
  async mounted () {
    // await this.fetchRooms()
  },
  methods: {
  }
}
</script>



